import {HashRouter as Router, Switch, Route, Redirect} from 'react-router-dom'
import './App.css';
import Home from "./Home";
import Foreword from "./Foreword";
import Success from "./Success";
import Failure from "./Failure";

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <Router>
          <Switch>
            <Route path="/100day">
              <Redirect to="/100day"/>
              <Foreword />
            </Route>
            <Route path="/success">
              <Redirect to="/success"/>
              <Success />
            </Route>
            <Route path="/failure">
              <Redirect to="/failure"/>
              <Failure />
            </Route>
            <Route path="/">
              <Redirect to="/"/>
              <Home />
            </Route>
          </Switch>
        </Router>
      </header>
    </div>
  );
}

export default App;
