import React from 'react'

function FormattedDate (props) {
  let beginDate = new Date()
  beginDate.setFullYear(2021, 5, 16)
  beginDate.setHours(0,0,0,0)
  let total = (props.date - beginDate) / 1000
  let day = parseInt(total / (60 * 60 * 24))
  let afterDay = total -  day * 60 * 60 * 24
  let hour = parseInt(afterDay / (60 * 60))
  let afterHour = afterDay - hour * 60 * 60
  let minute = parseInt(afterHour / 60)
  let second = parseInt(afterHour - minute * 60)
  return <p>认识虎贲已经 {day} 天 {hour} 小时 {minute} 分 {second} 秒啦！</p>
}

class Clock extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      date: new Date()
    }
  }

  componentDidMount () {
    this.timerID = setInterval(
      () => this.tick(),
      1000
    )
  }

  componentWillUnmount () {
    clearInterval(this.timerID)
  }

  tick () {
    this.setState({
      date: new Date()
    })
  }

  render () {
    return (
      <div>
        <FormattedDate date={this.state.date} />
      </div>
    )
  }
}

export default Clock
